.main-nav {
  @media (min-width: $desktop-width) {
    margin-top: 4px;
  }
}

.main-nav__button-toggle {
  @media (min-width: $desktop-width) {
    display: none;
  }
}

.main-nav__list {
  visibility: hidden;
  opacity: 0;
  transform: translateX(-100px);
  width: 0;
  height: 0;
  transition: transform 0.4s ease, visibility 0.4s ease;

  @include offset(0, 0);
  list-style: none;

  @media (min-width: $desktop-width) {
    visibility: visible;
    display: flex;
    flex-wrap: wrap;
  }
}

.main-nav__link {
  display: block;
  transition: 0.3s ease;
  font-weight: 500;
  color: $black1;

  @media (min-width: $desktop-width) {
    @include offset(9px 42px 12px 42px, 0);
    @include typography(14px, 16px);
    border-right: 1px solid $grey;
  }
}

