.main-nav__link:hover,
.main-nav__link:focus {
  background-color: $grey;
}

.main-nav__item:first-child .main-nav__link {
  @media (min-width: $desktop-width) {
    padding-left: 21px;
  }
}

@media (max-width: $mobile-width-only) {
  .main-nav__closed {
    width: 32px;
    height: 32px;
    margin-left: 16px;
    opacity: 1;
    transition: opacity 0.3s ease, width 0.3s ease, height 0.3s ease, visibility 0.3s;
  }
}

@media (max-width: $tablet-width-only) {
  .main-nav__closed .main-nav__button-toggle {
    position: relative;
    width: 32px;
    height: 32px;

    @include offset(0, 0);
    background-color: $grey;

    @media (min-width: $tablet-width) {
      width: 40px;
      height: 40px;
      margin-left: 0;
      border-radius: 3px;
    }
  }

  .main-nav__closed .main-nav__button-toggle::before {
    content: "";
    position: absolute;
    width: 14px;
    height: 2px;
    top: 15px;
    left: 9px;
    box-shadow: 0 -5px 0 0 $blue, 0 5px 0 0 $blue;
    background-color: $blue;
    z-index: 10;
    transition: 0.3s;

    @media (min-width: $tablet-width) {
      top: 19px;
      left: 14px;
    }
  }

  .main-nav__opened .main-nav__button-toggle {
    position: absolute;
    top: 6px;
    right: 16px;
    width: 32px;
    height: 32px;
    border-radius: 3px;

    @include offset(0, 0);
    background-color: $grey;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 15px;
      left: 9px;
      width: 13px;
      height: 2px;
      background-color: $red;
      transition: 0.3s;

      @media (min-width: $tablet-width) {
        top: 17px;
        left: 10px;
        width: 20px;
      }
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }

    @media (min-width: $tablet-width) {
      top: 17px;
      right: 37px;
      width: 40px;
      height: 40px;
      margin-left: 0;
      border-radius: 3px;
    }
  }

  .main-nav__opened .main-nav__list {
    visibility: visible;
    transform: translateY(0);
    opacity: 1;
    transition: all 0.3s ease;
    width: auto;
    height: auto;
    min-width: 288px;
    padding-bottom: 60px;
  }

  .main-nav__link {
    @include offset(24px 0 24px 0, 0);
    @include typography(16px, 19px);
    border-bottom: 1px solid $lightgrey3;
  }

  .main-nav__link--calc {
    padding-left: 30px;

    &::before {
      top: 24px;
      left: 0;
    }
  }
}
