.header {
  position: relative;
  padding: 6px 0;
  margin-bottom: 16px;
  background-color: $white;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $grey;

    @media (min-width: $desktop-width) {
      top: 69px;
    }
  }

  @media (min-width: $tablet-width) {
    padding: 10px 0;
  }

  @media (min-width: $desktop-width) {
    padding: 7px 0 0 0;
  }
}

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  transition: all 0.5s ease;

  @media (min-width: $tablet-width) {
    @include offset(0 12px 0 0, 0);
  }

  @media (min-width: $desktop-width) {
    width: 1321px;
    margin: 0 auto;
    padding-right: 8px;
  }
}

.header__logo {
  display: flex;

  @include offset(0, 0 22px 0 0);

  @media (min-width: $tablet-width) {
    margin-right: 56px;
  }

  @media (min-width: $desktop-width) {
    margin-right: 73px;
  }
}

.header__logo-img {
  width: 116px;
  height: 28px;

  @media (min-width: $tablet-width) {
    width: 209px;
    height: 50px;
  }

  @media (min-width: $desktop-width) {
    width: 254px;
    height: 56px;
  }
}

@media (max-width: $tablet-width-only) {
  .header__wrapper--open-menu {
    flex-direction: column;
    align-items: flex-start;
    margin-top: 42px;
    transition: all 0.4s ease;
  }

  .header__wrapper--open-menu > .form-search {
    display: none;
    width: 0;
    height: 0;
  }

  .header__wrapper--open-menu .header__logo-img {
    display: none;
  }

